@font-face {
  font-family: 'Nimbus San L';
  src: url("../fonts/nimbussansl_bold_macroman/NimbusSanL-Bol-webfont.eot");
  src: url("../fonts/nimbussansl_bold_macroman/NimbusSanL-Bol-webfont.eot") format("eot"), url("../fonts/nimbussansl_bold_macroman/NimbusSanL-Bol-webfont.woff") format("woff"), url("../fonts/nimbussansl_bold_macroman/NimbusSanL-Bol-webfont.ttf") format("truetype");
}

.app {
  text-align: center;
  height: 100%;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  background-color: #333333;
}

.loading {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.logo {
  background-color: #648e3d;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  height: 200px;
  width: 200px;
  font-family: "Nimbus San L", sans-serif;
  font-weight: bold;
  font-size: 40px;
  text-align: center;
  color: white;
}

.logo.animated {
  animation: logo-animation infinite 10s linear alternate;
}

@keyframes logo-animation {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.25);
  }
}

.view-box {
  flex-grow: 1;
  position: relative;
}

.view-box div {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-size: cover;
  background-position: center;
}

.view-box embed {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.text-box {
  min-height: 100px;
  border: 7px solid transparent;
  border-image: url(../images/TextBoxFrame.png) 7 fill round;
  padding: 1em;
}

.text-box .content {
  text-align: center;
  font-weight: bold;
}

.text-box .choices {
  list-style: none;
  margin-bottom: 0;
  padding: 0;
}

.text-box .choices li {
  color: dodgerblue;
  padding: 5px;
  cursor: pointer;
}




